import { _decorator, Component, Node, tween, v3, view } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('NewComponent')
export class NewComponent extends Component {
    // 绑定玩家节点
    @property({type: Node})
    playerNode: Node = null
    height: number = 0
    
    start() {
        this.placePlayer()
    }

    update(deltaTime: number) {
        
    }

    placePlayer(){
        // 获取屏幕可见区域的宽高
        this.height = view.getVisibleSize().height
        console.log(this.height,"高度")
        this.playerNode.setPosition(v3(0,-this.height/4))
    }

    // 发射
    fire(){
        let tweenDuration = 0.6
        tween(this.node).to(tweenDuration,{
            position:v3(0,this.height/2)
        })
    }
}

